<template>
    <section class="content">
        <div class="container-fluid">
            <div class="row">
                <div class="col-12">
                    <div class="card">
                        <div class="card-header">
                            <h3 class="card-title">Machine List</h3>
                            <div class="card-tools">


                            </div>
                        </div>
                        <!-- /.card-header -->
                        <div class="card-body table-responsive p-0">
                            <table class="table table-hover">
                                <thead>
                                    <tr>
                                        <th width="40">FAR No </th>
                                        <th width="40">PM Status</th>
                                        <th width="20">Action</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="Machine in Machines.data" :key="Machine.id">

                                        <td class="align-middle">{{ Machine.far_no }}</td>
                                        <td @click="view_pm_details(Machine)" class="align-middle">
                                            <VueGauge :refid="'mg-id'+Machine.id"
                                                :options="{'needleValue': Machine.next_pm_date.early,'arcDelimiters':[10,36,78] ,arcColors:['red','orange','yellow','green'] , chartWidth:'80'}" />
                                        </td>
                                        <td class="align-middle"> 

                                            <a href="javascript:void(0);" @click="get_details(Machine.id)"
                                                class="btn btn-primary btn-sm">
                                                View Details
                                            </a>
                                            <a href="javascript:void(0);" @click="addModal(Machine)"
                                                class="btn btn-success btn-sm" v-if="Machine.current_site">
                                                Add Details
                                            </a> 
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                        <!-- /.card-body -->
                        <div class="card-footer">
                            <pagination :data="Machines" @pagination-change-page="getResults" v-if="Machines">
                            </pagination>
                        </div>
                    </div>
                    <!-- /.card -->
                </div>
            </div>


        </div>

        <div class="modal" id="maintenance_modal">
            <div class="modal-dialog modal-lg">
                <div class="modal-content">

                    <!-- Modal Header -->
                    <div class="modal-header">
                        <h4 class="modal-title">Maintenance List</h4>
                        <button type="button" class="close" data-dismiss="modal">&times;</button>
                    </div>

                    <!-- Modal body -->
                    <div class="modal-body">
                        <table class="table table-striped">
                            <thead>
                                <tr>
                                    <th>Date Time</th>
                                    <th>Run Hour</th>
                                    <th>KM Run</th>
                                    <th>Production Fig</th>
                                    <th>Consumption</th>
                                    <th>Action</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="row in maintenance" :key="row.id">
                                    <td>{{ row.datetime }}</td>
                                    <td>{{ row.run_hour }}</td>
                                    <td>{{ row.km_run }}</td>
                                    <td>{{ row.production_fig }}</td>
                                    <td>{{ row.consumption }}</td>
                                    <td>
                                        <a href="javascript:void(0);" @click="deleteMaintenance(row.id)">
                                            <i class="fa fa-trash red"></i>
                                        </a>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>

                    <!-- Modal footer -->
                    <div class="modal-footer">
                        <button type="button" class="btn btn-danger" data-dismiss="modal">Close</button>
                    </div>

                </div>
            </div>
        </div>



        <div class="modal fade" id="addDetails" tabindex="-1" role="dialog" aria-labelledby="addDetails"
            aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">Add Maintenance</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>

                    <form @submit.prevent="submitMaintenance()">
                        <div class="modal-body">
                            <div class="row">
                                <div class="col-6">
                                    <div class="form-group">
                                        <label>Date</label>
                                        <datetime v-model="form.datetime" name="datetime" input-class="form-control"
                                            :class="{ 'is-invalid': verrors.has('datetime') }" v-validate="'required'"
                                            data-vv-as="date time" type="datetime" />
                                        <div v-if="verrors.has('datetime')" class="help-block invalid-feedback">{{
                                        verrors.first('datetime')
                                        }}</div>
                                    </div>
                                </div>
                                <div class="col-6" v-if="pop_machine.is_consumtion_hr_run">
                                    <div class="form-group">
                                        <label>Current Run hour </label>
                                        <input v-model="form.run_hour" name="run_hour" class="form-control"
                                            :class="{ 'is-invalid': verrors.has('run_hour') }"
                                            v-validate="'required|decimal:2'" data-vv-as="Current Run hour" />
                                        <div v-if="verrors.has('run_hour')" class="help-block invalid-feedback">
                                            {{ verrors.first('run_hour') }}</div>
                                    </div>
                                </div>
                                <div class="col-6" v-if="pop_machine.is_consumtion_km_run">
                                    <div class="form-group">
                                        <label> Current KM Run </label>
                                        <input v-model="form.km_run" name="km_run" class="form-control"
                                            :class="{ 'is-invalid': verrors.has('km_run') }"
                                            v-validate="'required|decimal:2'" data-vv-as="Current KM hour" />
                                        <div v-if="verrors.has('km_run')" class="help-block invalid-feedback">{{
                                        verrors.first('km_run')
                                        }}</div>
                                    </div>
                                </div>
                                <div class="col-6" v-if="pop_machine.is_production_fig">
                                    <div class="form-group">
                                        <label> Current Production fig </label>
                                        <input v-model="form.production_fig" name="production_fig" class="form-control"
                                            :class="{ 'is-invalid': verrors.has('production_fig') }"
                                            v-validate="'required|decimal:2'" data-vv-as="Current Production fig" />
                                        <div v-if="verrors.has('production_fig')" class="help-block invalid-feedback">{{
                                        verrors.first('production_fig')
                                        }}</div>
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="form-group">
                                        <label>Fuel consumption</label>
                                        <input v-model="form.consumption" name="consumption" class="form-control"
                                            :class="{ 'is-invalid': verrors.has('consumption') }"
                                            v-validate="'required|decimal:2'" data-vv-as="Fuel consumption" />
                                        <div v-if="verrors.has('consumption')" class="help-block invalid-feedback">
                                            {{ verrors.first('consumption') }}</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                            <button type="submit" class="btn btn-primary">Submit</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>

        <!-- Modal -->
        <div class="modal fade" id="pmModal" tabindex="-1" role="dialog" aria-labelledby="pmModal" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">View PM Details</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>

                    <div class="modal-body">


                        <div class="container">
                            <p>
                                <strong>Far No :</strong> {{pmModalData.far_no}}
                                </br>
                                <strong>Machine Name :</strong> {{pmModalData.name}}
                            </p>

                            <div class="row">
                                <div class="col-sm pb-3" v-if="pmModalData.is_maintenance_date">
                                    <VueGauge refid="popg1"
                                        :options="{ 'needleValue': pmModalData.next_pm_date.maintenance_date, 'arcDelimiters': [Number(pmModalData.next_pm_date.settings.date_red), Number(pmModalData.next_pm_date.settings.date_orange), Number(pmModalData.next_pm_date.settings.date_yellow)], arcColors: ['red', 'orange', 'yellow', 'green'], chartWidth: '180' ,rangeLabel: ['0', pmModalData.date_threshold.toString()],arcLabels:[pmModalData.next_pm_date.settings.date_red.toString(), pmModalData.next_pm_date.settings.date_orange.toString(), pmModalData.next_pm_date.settings.date_yellow.toString(), pmModalData.next_pm_date.settings.date_green.toString()],arcOverEffect:true}" />
                                    <p class="text-center">Remaining Days</p>
                                </div>
                                <div class="col-sm pb-3" v-if="pmModalData.is_consumtion_km_run">
                                    <VueGauge refid="popg2"
                                        :options="{ 'needleValue': pmModalData.next_pm_date.km_run, 'arcDelimiters': get_del(pmModalData.run_km_threshold,Number(pmModalData.next_pm_date.settings.km_red),Number(pmModalData.next_pm_date.settings.km_orange),Number(pmModalData.next_pm_date.settings.km_yellow)),
                                        arcColors: ['red', 'orange', 'yellow', 'green'],
                                        chartWidth: '180',
                                        rangeLabel: ['0', pmModalData.run_km_threshold.toString()],arcLabels:[pmModalData.next_pm_date.settings.km_red.toString(), pmModalData.next_pm_date.settings.km_orange.toString(), pmModalData.next_pm_date.settings.km_yellow.toString(), pmModalData.next_pm_date.settings.km_green.toString()],arcOverEffect:true}" />
                                    <p class="text-center">Remaining KM</p>
                                </div>
                                <div class="col-sm" v-if="pmModalData.is_consumtion_hr_run">
                                    <VueGauge refid="popg3"
                                        :options="{ 'needleValue': pmModalData.next_pm_date.hr_run, 'arcDelimiters': get_del(pmModalData.run_hour_threshold,Number(pmModalData.next_pm_date.settings.hr_red), Number(pmModalData.next_pm_date.settings.hr_orange), Number(pmModalData.next_pm_date.settings.hr_yellow)), arcColors: ['red', 'orange', 'yellow', 'green'], chartWidth: '180' ,rangeLabel: ['0', pmModalData.run_hour_threshold.toString()],arcLabels:[pmModalData.next_pm_date.settings.hr_red.toString(), pmModalData.next_pm_date.settings.hr_orange.toString(), pmModalData.next_pm_date.settings.hr_yellow.toString(), pmModalData.next_pm_date.settings.hr_green.toString()],arcOverEffect:true}" />

                                    <p class="text-center">Remaining Hours</p>
                                </div>
                                <div class="col-sm" v-if="pmModalData.is_production_fig">
                                    <VueGauge refid="popg4"
                                        :options="{ 'needleValue': pmModalData.next_pm_date.production_fig, 'arcDelimiters': get_del(pmModalData.production_fig_threshold,Number(pmModalData.next_pm_date.settings.prdfig_red), Number(pmModalData.next_pm_date.settings.prdfig_orange), Number(pmModalData.next_pm_date.settings.prdfig_yellow)), arcColors: ['red', 'orange', 'yellow', 'green'], chartWidth: '180' ,rangeLabel: ['0', pmModalData.production_fig_threshold.toString()],arcLabels:[pmModalData.next_pm_date.settings.prdfig_red.toString(), pmModalData.next_pm_date.settings.prdfig_orange.toString(), pmModalData.next_pm_date.settings.prdfig_yellow.toString(), pmModalData.next_pm_date.settings.prdfig_green.toString()],arcOverEffect:true}" />
                                    <p class="text-center">Remaining Production</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>



    </section>
</template>

<script>

import VueGauge from 'vue-gauge';
export default {
    name: "MachineMaintenance",
    components: {
        VueGauge,
    },
    data() {
        return {
            Machines: {},
            maintenance: '',
            pmModalData: '',
            form: new Form({
                id: '',
                machine_id: '',
                datetime: '',
                run_hour: '',
                km_run: '',
                production_fig: '',
                consumption: '',
            }),
            pop_machine: '',

        }
    },
    methods: {
        get_del(threshold, red, orange, yellow) {
            let x = threshold / 30;
            let a = [
                x - (x * red / 100),
                x - (x * orange / 100),
                x - (x * yellow / 100)
            ]
            return a;
        },
        async view_pm_details(data) {
            this.pmModalData = data
            $('#pmModal').modal('show');
        },
        get_details(id) {
            let pop_loader = this.$loading.show();
            this.form.get('/api/maintenance/' + id)
                .then((response) => {
                    this.maintenance = response.data.data.maintenances
                    $('#maintenance_modal').modal('show');
                    pop_loader.hide()
                })
                .catch(() => {
                    pop_loader.hide()
                })
        },
        getResults(page = 1) {
            axios.get('api/machine?page=' + page).then(({ data }) => (this.Machines = data.data));
        },
        loadMachines() {
            axios.get("api/machine").then(({ data }) => (this.Machines = data.data));
        },
        addModal(machine) {
            this.$validator.reset();
            this.form.reset();
            $('#addDetails').modal('show');
            this.form.machine_id = machine.id;
            this.pop_machine = machine
        },
        submitMaintenance() {
            this.$validator.validate().then(async (valid) => {
                if (valid) {
                    let pop_loader = this.$loading.show();

                    this.form.post('/api/maintenance')
                        .then((response) => {
                            if (response.data.success) {
                                $('#addDetails').modal('hide');
                                this.$validator.reset();
                                this.form.reset();
                                Toast.fire({
                                    icon: 'success',
                                    title: response.data.message
                                });

                                this.loadTickets();
                            }
                            pop_loader.hide()
                        })
                        .catch(() => {
                            pop_loader.hide()
                            if (typeof err.response.data != 'undefined') {
                                this.$setErrorsFromResponse(err.response.data);
                            }
                        })
                }
            });
        },
        deleteMaintenance(id) {
            Swal.fire({
                title: 'Are you sure?',
                text: "You won't be able to revert this!",
                showCancelButton: true,
                confirmButtonColor: '#d33',
                cancelButtonColor: '#3085d6',
                confirmButtonText: 'Yes, delete it!'
            }).then((result) => {
                // Send request to the server
                if (result.value) {
                    this.form.delete('api/maintenance/' + id).then((response) => {
                        if (response.data.data && response.data.data.length) {
                            this.get_details(response.data.data[0].id);
                        }
                        Swal.fire(
                            'Deleted!',
                            'Your record has been deleted.',
                            'success'
                        );
                    }).catch((data) => {
                        Swal.fire("Failed!", data.message, "warning");
                    });
                }
            })
        }
    },
    mounted() {

    },
    created() {
        this.loadMachines();
    },
}
</script>
